import React, { useState, useContext, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { Form, Button } from 'react-bootstrap';
import { addPresentation } from '../../redux/actions/presentationActions';

export const ProfilePresentation = () => {

    const pres = useSelector((state) => state.presentation.presentation);

    const { title, subtitle, image, description } = pres[0] ?? {};

    const dispatch = useDispatch();

    const fetchPresentation = async () => {
        const response = await axios
            .get('http://localhost:3005/presentation')
            .catch((err) => {
                console.log('Err', err);
            });
        dispatch(addPresentation(response.data));
    }

    const putPresentation = async (tit, subtit, descr) => {

        const newPresentation = {
            title: tit,
            subtitle: subtit,
            description: descr
        }

        const response = await axios
            .put('http://localhost:3005/presentation', newPresentation)
            .then(response => {
                console.log(response.data);
            })
            .catch((err) => {
                console.log('Err', err);
            });
        
    }

    const submitPresentationEdit = (event) => {
        event.preventDefault();
        putPresentation(event.target.title.value, event.target.subtitle.value, event.target.description.value);
    }

    const submitPresentationChange = (event) => {
        event.preventDefault();
        fetchPresentation();
    }

    useEffect(() => {
        fetchPresentation();
    }, []);

    return (
        <>
            { /* Presentation */}
            <div className="story clearfix">
                <h2 className="small-heading">{title}</h2>
                <div className="col-lg-11 col-lg-offset-1">
                    <div className="story-content clearfix">
                        <img alt="" src={image} className="col-xs-offset-1 col-sm-offset-0 col-sm-4 col-xs-10" />
                        <div className="col-sm-8 col-xs-12">
                            <h3>{subtitle}</h3>
                            <p>
                                {description}
                            </p>
                            <a href="#" className="hire-me">Hire Me</a>
                            <a href="#">Download Resume</a>
                        </div>
                    </div>
                </div>

                <Form onSubmit={submitPresentationEdit} onChange={submitPresentationChange} id="presentationEditFormId">
                    <Form.Row>
                        <Form.Group className={"half"}>
                            <Form.Label>Title</Form.Label>
                            <Form.Control
                                name="title"
                                autoComplete="off"
                                as="textarea"
                                rows={1} />
                        </Form.Group>
                        <Form.Group className={"half"}>
                            <Form.Label>Subtitle</Form.Label>
                            <Form.Control
                                name="subtitle"
                                autoComplete="off"
                                as="textarea"
                                rows={1} />
                        </Form.Group>
                    </Form.Row>
                    <Form.Group>
                        <Form.Label>Description</Form.Label>
                        <Form.Control
                            name="description"
                            autoComplete="off"
                            as="textarea"
                            rows={5} />
                    </Form.Group>
                    <Button
                        variant="success"
                        type="submit"
                        size="sm" >
                        Submit
                    </Button>
                </Form>

            </div>
        </>
    );
}